import React from 'react';
import {DragDropContext, Droppable} from "react-beautiful-dnd";

const reorder = (list, oldIndex, newIndex) => {
    // Táº¡o má»t báº£n sao chÃ©p tá»« danh sÃ¡ch cÅ©
    const temps = [...list];
    // XÃ³a pháº§n tá»­ á» vá» trÃ­ cÅ© => tráº£ vá» máº£ng pháº§n tá»­ ÄÃ£ xÃ³a
    const [removed] = temps.splice(oldIndex, 1);
    // ThÃªm pháº§n tá»­ ÄÃ£ xÃ³a khá»i vá» trÃ­ cÅ© vÃ o vá» trÃ­ má»i
    temps.splice(newIndex, 0, removed);

    return temps;
};

export default function DragSortable(props) {

    const {items, onDragEnd, droppableId, data} = props;

    const onDragEndHandle = (result) => {
        // dropped outside the list
        if (!result.destination) {
            return;
        }

        const temps = reorder(
            items, // Danh sÃ¡ch cÃ¡c pháº§n tá»­
            result.source.index, // Vá» trÃ­ cÅ©
            result.destination.index // Vá» trÃ­ má»i
        );

        const value = data || null;
        onDragEnd(temps, value);
    };

    return (
        <DragDropContext onDragEnd={onDragEndHandle}>
            <Droppable droppableId={droppableId}>
                {(provided, snapshot) => (
                    <div
                        {...provided.droppableProps}
                        ref={provided.innerRef}
                        className={snapshot.isDraggingOver ? "drag-sortable draging-over" : "drag-sortable"}
                    >
                        {
                            props.children
                        }
                        {
                            provided.placeholder
                        }
                    </div>
                )}
            </Droppable>
        </DragDropContext>
    )
}
