import { useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import { message } from 'antd';
import httpServ from '../../services/http.service';
import { NavBar } from '../common';
import ExamDesktopForm from '../../pages/ExamDesktop/ExamDesktopForm/ExamDesktopForm';

function checkTimeAgainstCurrentDate(targetDate) {
    // Láº¥y ngÃ y hiá»n táº¡i
    const currentDate = new Date();
    // Chuyá»n Äá»i targetDate thÃ nh Äá»i tÆ°á»£ng Date (náº¿u nÃ³ khÃ´ng pháº£i lÃ  Äá»i tÆ°á»£ng Date)
    if (!(targetDate instanceof Date)) {
        targetDate = new Date(targetDate);
    }
    // So sÃ¡nh ngÃ y vÃ  giá»
    if (targetDate > currentDate) {
        // "targetDate náº±m trong tÆ°Æ¡ng lai.";
        return -1;
    } else if (targetDate < currentDate) {
        // "targetDate náº±m trong quÃ¡ khá»©.";
        return 1;
    } else {
        // "targetDate lÃ  ngÃ y hiá»n táº¡i!";
        return 0;
    }
}

const dateToString = (ngayKichHoat) => {
    const date = new Date(ngayKichHoat);
    return `${date.getDate()}/${date.getMonth() + 1}/${date.getFullYear()}`;
}

function ExamTestListMobile(props) {
    let userInfo = useSelector((state) => state.authUser.userInfor);

    const [visibled, setVisibled] = useState(false);
    const [baiKiemTra, setBaiKiemTra] = useState(null);
    const [danhSachDeThi, setDanhSachDeThi] = useState([]);

    useEffect(() => {
        httpServ.getDeThiTheoNguoiDung(userInfo.id)
            .then(res => {
                const data = res.data.content;
                setDanhSachDeThi(data) 
            })
            .catch(err => {
                console.log(err);
            })
    }, [])

    const renderButton = (record) => {
        const ketQua = checkTimeAgainstCurrentDate(record.ngayKichHoat);
        if (record.daNopBai)
            return <button className="btn-success"> ÄÃ£ ná»p bÃ i </button>;
        else if (record.kichHoat)
            return <button className="btn-info" onClick={() => handleDoWork(record)}>LÃ m bÃ i</button>
        else if (ketQua == -1)
            return <button className="btn-dark">ChÆ°a kÃ­ch hoáº¡t</button>
        else if (ketQua == 1)
            return <button className="btn-danger">ÄÃ£ quÃ¡ háº¡n</button>
    }

    const handleDoWork = (deThi) => {
        if (!deThi.kichHoat) return message.error('BÃ i kiá»m tra chÆ°a kÃ­ch hoáº¡t do chÆ°a Äáº¿n thá»i gian thá»±c hiá»n!');

        setVisibled(true);
        setBaiKiemTra(deThi);
    }

    return (
        <>
            <NavBar title="BÃ i kiá»m tra" />
            <div className="ExamDesktop">
                {
                    danhSachDeThi.map((item, i) => {
                        return <div className='ExamDesktopItem' key={i}>
                            <div className='ExamDesktopItem-Title'>
                                {item.tenDeThi}
                            </div>
                            <div className='ExamDesktopItem-Info'>
                                <div className='ExamDesktopItem-InfoTime'>
                                    <span>NgÃ y thá»±c hiá»n: {dateToString(item.ngayKichHoat)}</span>
                                    <span>Thá»i gian lÃ m bÃ i: 45 phÃºt</span>
                                </div>
                                <div className='ExamDesktopItem-Button'>
                                    {
                                        renderButton(item)
                                    }
                                </div>
                            </div>
                        </div>
                    })
                }
                {
                    visibled ? <ExamDesktopForm
                        baiKiemTra={baiKiemTra}
                        handleClose={setVisibled}
                    /> : null
                }

            </div>
        </>
    )
}

export default ExamTestListMobile;