import { useEffect, useState } from "react"
import httpServ from "../../../services/http.service";
import { message } from "antd";

export default function ChangePassword(props) {

    const [model, setModel] = useState({ email: '', password: '' });
    const [confirm, setConfirm] = useState('');
    const [errorMessage, setErrorMessage] = useState('');

    useEffect(() => {
        setErrorMessage('');
        setConfirm('');
        setModel({ email: '', password: '' })
    }, [])

    const handleSubmit = () => {
        setErrorMessage('');

        if (!model.email)
            return setErrorMessage('Vui lÃ²ng nháº­p email!');
        if (!model.password)
            return setErrorMessage('Vui lÃ²ng nháº­p máº­t kháº©u!');
        if (confirm != model.password)
            return setErrorMessage('Máº­t kháº©u nháº­p láº¡i khÃ´ng khá»p!');

        httpServ.putChangePassword(model)
        .then(res => {
            message.success('Cáº­p nháº­t máº­t kháº©u thÃ nh cÃ´ng!');
            props.handleCloseModal();
        })
        .catch(error => {
            if(error.err.response.data.content)
                setErrorMessage(error.err.response.data.content);
        })
    }

    return <div className="change-password">
        <div className="change-password-title">
            Äá»I Máº¬T KHáº¨U
        </div>
        {errorMessage ? <div className="change-password-message">{errorMessage}</div> : null}
        <div className="change-password-input">
            <label>Email</label>
            <input type="email" value={model.email} onChange={(e) => setModel({ ...model, email: e.target.value })} />
        </div>
        <div className="change-password-input">
            <label>Máº­t kháº©u má»i</label>
            <input type="password" value={model.password} onChange={(e) => setModel({ ...model, password: e.target.value })} />
        </div>
        <div className="change-password-input">
            <label>Nháº­p láº¡i máº­t kháº©u</label>
            <input type="password" value={confirm} onChange={(e) => setConfirm(e.target.value)} />
        </div>
        <div className="change-password-button">
            <button onClick={(handleSubmit)}>Cáº­p nháº­t</button>
        </div>
    </div>
}